.program__index {
  position: relative;

  @media (min-width: $desktop-width) {
    margin-bottom: 82px;
  }
}

.program__index::before {
  background-image: url("../img/index-background-phone@1x.jpg");
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 302px;
  content: "";
  background-size: cover;
  z-index: -1;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/index-background-phone@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/index-background-desktop@1x.jpg");
    display: inline-block;
    top: -150px;
    width: 50%;
    height: 658px;
  }

  @media (min-width: $desktop-width) and (min-resolution: 2dppx) {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}

.program__container {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    padding: 0 118px 480px 118px;
    position: relative;
  }

  @media (min-width: $desktop-width) {
    padding: 0 190px 165px 190px;
    position: relative;
  }
}

.program__form-wrapper {
  @media (min-width: $tablet-width) {
    padding-left: 67px;
    padding-right: 70px;
  }
}

.program__title {
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin: 27px auto 40px 0;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    margin: 40px 0 54px 0;
    font-size: 60px;
    line-height: 60px;
  }
}

.program__name {
  padding: 27px 0 0 0;
  margin: 0 0 25px 0;
  display: flex;
  text-align: center;
  justify-content: center;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    color: #000000;
    width: 450px;
    text-align: left;
    padding: 62px 0 0 0;
    margin: 0 0 41px 0;
    font-size: 60px;
    line-height: 60px;
  }
}

.program__slogan {
  text-transform: uppercase;
  padding: 0;
  margin: 0 0 39px 0;
  font-size: 14px;
  line-height: 18px;

  @media (min-width: $tablet-width) {
    max-width: 339px;
    margin: 0 0 66px 0;
    font-size: 20px;
    line-height: 30px;
  }
}

.program__intro {
  text-transform: uppercase;
  padding: 0;
  margin: 0 0 39px 0;
  display: flex;
  justify-content: center;
  font-size: 14px;
  line-height: 14px;

  @media (min-width: $tablet-width) {
    color: #000000;
    width: 300px;
    justify-content: left;
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 52px;
  }
}

.program__image {
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    top: 256px;
    right: 3%;
    height: 536px;
    width: 630px;
    z-index: -1;
  }

  @media (min-width: $desktop-width) {
    top: -20px;
    left: 270px;
    height: 499px;
    width: 552px;
  }
}

.program__button {
  @media (min-width: $tablet-width) {
    max-width: 244px;
    padding: 14px 26px;
  }
}
