.site-list {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    vertical-align: middle;
  }
}

.site-list__link {
  display: flex;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  padding: 22px;
  margin: 0;
  justify-content: center;
  white-space: nowrap;
  border-top: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    margin: 0 17px;
    padding: 0;
    border: none;
  }
}

.site-list__link:hover {
  opacity: 0.6;
}

.site-list__link:active {
  opacity: 0.3;
}

.site-list__link-index {
  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}

.site-list__link-current {
  position: relative;
}

.site-list__link-current::before {
  @media (min-width: $tablet-width) {
    color: #ffffff;
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -8px;
    left: 0;
    background: $green;
  }

  @media (min-width: $desktop-width) {
    background: #ffffff;
  }
}
